@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;1,100;1,200&family=Ubuntu:ital,wght@0,700;1,300;1,400&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* color: linear-gradient(to right,#2FB8FF, #9EECD9); */
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(black, #0e0f11);
}
.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: white;
}
.coin-search{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 64px;
  justify-content: center;
}
.coin-text{
  margin-bottom: 22px;
  text-align: center;
  background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 25px;
  background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  border-color: linear-gradient(to right, #53a5f7, #9EECD9);;

}
.coin-input::-webkit-input-placeholder{
  color: white;
}
.coin-input::placeholder{
  color: white;
}
.coin-container{
    display: flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid rgb(153, 151, 151);
    width: 900px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 44px;
    max-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol{
    text-transform: uppercase;
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    
}
.coin-name{
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.coin-price{
    width: 110px;
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.coin-volume{
    width: 205px;
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.coin-marketcap{
    width: 240px;
    background: linear-gradient(to right, #53a5f7, #9EECD9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.coin-percent{
    width: 80px;
    
}

.red{
    color: red;
}

.green{
    color: lightgreen;
}
